import { Loading, LoadingController, ToastController ,Toast , AlertController ,Alert} from 'ionic-angular'


/**
 * UI 
 * 
 * @export
 * @abstract
 * @class BaseUi
 */
export abstract class BaseUi {
    isShowTab : boolean;

    constructor() {
    }
    
    /**
     *  loading 
     * 
     * @protected
     * @param {LoadingController} loadingCtrl 
     * @param {string} message 
     * @returns {Loading} 
     * @memberof BaseUi
     */
    protected showLoading(loadingCtrl : LoadingController,message : string) : Loading{
        let loader = loadingCtrl.create({
            content : message ,
            dismissOnPageChange : true  //loading
        });
        loader.present()
        return loader
    }

    /**
     *  toast 
     * 
     * @protected
     * @param {ToastController} toastCtrl  3toast
     * @param {string} message 
     * @returns {Toast} 
     * @memberof BaseUi
     */
    protected showToast(toastCtrl : ToastController , message : string) : Toast{
        let toast = toastCtrl.create({
            message : message ,
            duration : 3000 , 
            position : 'bottom' 
        })
        toast.present();
        return toast;
    }

    /**
     *  alert 
     * 
     * @param alertCtrl     alert
     * @param til   
     * @param content   
     * @param btnName   
     */
    protected showAlert(alertCtrl : AlertController , til : string , content : string , btnName : string) : Alert{
        let alert = alertCtrl.create({
            title : til,
            subTitle : content ,
            buttons: [btnName] 
        })
        alert.present();
        return alert;
    }
}


